<template>
  <div class="about">
    <form @submit.prevent="postData">
      <input type="text" v-model="name" id="">
      <input type="text" v-model="age" id="">
      <input type="submit" value="Submit">
    </form>
  </div>
</template>

<script>
/*eslint-disable*/
import axios from 'axios'
export default {
    data(){
      return{
        name:'',
        age:''
      }
    },
    methods:{
      postData(){
        let data = {
          name: this.name,
          age:this.age
        }
        axios.post('api/add', data).then(response =>{
                   this.$router.push('/')
        })
      }
    }
    
}
</script>
